import React, {Component} from 'react';
import Section from './../components/Section';
import CalendarFeed from './../components/CalendarFeed';
import ContentFeed from './../components/ContentFeed';
import Footer from './../components/Footer';
import {Image, Embed} from 'semantic-ui-react';

export default class Home extends Component{
    render(){
        return(
            <div id="sobre" style={{marginTop:'-10px'}}>
                <Image style={{zIndex:'-10'}} src="http://res.cloudinary.com/dkbuneg9h/image/upload/v1511745793/background-img-2_mokgdf_tkq3zv.jpg"/>
                <Image style={{marginTop: '4em'}}size="small" centered src="http://res.cloudinary.com/dkbuneg9h/image/upload/v1477079274/pet/pet_wn9jqn.png"/>
                <Section>
                    <p style={{marginBottom: '2em'}}>Celulares e computadores esto dominando o mundo. Quase tudo o que fazemos hoje requer alguma forma de programao. Mas no estamos fazendo o suficiente nas escolas para encorajar o aprendizado da computao e preparar nossos alunos para este futuro!</p>
					<Embed id="mHW1Hsqlp6A" source='youtube' placeholder="http://res.cloudinary.com/dkbuneg9h/image/upload/v1511325108/Ruchi-facebook-first-female-enginer_min_r2pssr.jpg"/>
                    <p style={{marginTop: '2em'}}>Quer comear a fazer parte deste mundo? O <a href="https://pet-informatica.github.io">PET-Informtica</a> pode ajudar. Participe do Programa de Frias e envolva-se conosco em curso introdutrio muito divertido!</p>
                </Section>
                <div style={{background:'#fafafa'}}>
                    <Image style={{marginTop: '4em'}} src="http://res.cloudinary.com/dkbuneg9h/image/upload/c_crop,h_568,w_1920,y_256/v1511797774/education-technology-school-and-people-concept-group-of-smiling-students-working-with-computers-in-computer-class-at-school_vuletns3e__F0000_oahbzu.png"/>
                    <Section title="SOBRE" marged>
                        <p>Todo ano, durante o perodo de frias escolares, o <a href="https://pet-informatica.github.io">PET-Informtica</a> ministra o programa de frias, um curso de <a href="https://www.python.org/">Python</a> gratuito voltado para alunos do ensino mdio e estudantes universitrios que se interessem por aprender programao.  uma forma de difundir o conhecimento da computao e aproximar estudantes de um mundo cada vez mais tecnolgico. Venha aprender uma linguagem de programao conosco! Vamos te mostrar que programar no  s coisa de quem faz computao! O curso ser ministrado no <a href="https://www.google.com.br/maps/place/CIn+-+Centro+de+Inform%C3%A1tica+-+UFPE/@-8.0574351,-34.9618738,14.38z/data=!4m5!3m4!1s0x7ab1be2115430ab:0x20ceccca91a11dd3!8m2!3d-8.0556681!4d-34.951578">Centro de Informtica</a>.</p>
                    </Section>
                    <div id="calendario"/>
                    <Section title="CALENDRIO" marged unjustify undivided font='16px'>
                        <CalendarFeed/>
                    </Section>
                    <div id="ementa"/>
                    {/*<Section title="EMENTA" marged untexted undivided>
                        <ContentFeed/>
                    </Section>*/}

                    <Footer/>
                </div>
            </div>
        );
    }
}


