import React, {Component} from 'react';
import {Grid, Card} from 'semantic-ui-react';

export default class ContentFeed extends Component{
    render(){
        return(
            <Grid columns={2} stackable>
                <Grid.Row>
                    <Grid.Column>
                        <Card fluid>
                            <Card.Content header as="h2" textAlign='center'>Ensino Mdio</Card.Content>
                            <Card.Content description style={{fontSize:'18px'}}>Alunos de ensino mdio devero aprender o bsico sobre programao, como conceitos gerais, lgica de programao, entrada e sada, strings, estruturas de dados, modularizao e uma introduo  orientao objeto. A ementa completa e detalhada do curso ser postada aqui em breve.</Card.Content>
                        </Card>
                    </Grid.Column>
                    <Grid.Column>
                        <Card fluid>
                            <Card.Content header as="h2" textAlign='center'>Universitrios</Card.Content>
                            <Card.Content decription style={{fontSize:'18px'}}>Alunos universitrios devem aprender tudo o que  ensinado aos de ensino mdio, porm com um foco maior na utilizao de bibliotecas e funes voltadas para construo de grficos, anlise de dados, estatstica e ecnomia. A ementa completa e detalhada do curso ser postada aqui em breve.</Card.Content>
                        </Card>
                    </Grid.Column>
                </Grid.Row>
            </Grid>
        );
    }
}


